@import url("https://fonts.googleapis.com/css?family=Merriweather");
html {
  height: 100%;
  margin: 0; }

body {
  height: 100%;
  margin: 0;
  color: #d17b0f;
  background-color: #ffffff;
  font-family: 'Merriweather', serif; }
  body .col-lg-4, body .col-sm-4 {
    padding: 20px;
    border-bottom: 1px solid; }
  body nav {
    min-width: 100%;
    padding: 10px;
    font-weight: bolder;
    font-size: 16px;
    margin-bottom: 15px;
    background: #f0e68c; }
    body nav p {
      font-size: 0.65em;
      font-style: italic; }
  body .wrapper {
    min-height: 100%;
    margin-bottom: -50px; }
  body .fa-arrow-up, body .fa-arrow-down {
    margin: 3px;
    float: right;
    cursor: pointer; }
  body .footer, body .push {
    height: 50px; }
  body .container {
    text-align: center; }
    body .container form input[type="text"] {
      color: #d17b0f;
      border: none;
      padding: 5px;
      border: 2px solid #9a031e;
      display: inline-block;
      margin-left: 10px; }
    body .container form button#submitButton {
      display: block;
      padding: 10px 15px;
      background-color: #9a031e;
      border: 1px solid #9a031e;
      border-radius: 3px;
      color: #d17b0f;
      margin: 0 auto;
      font-weight: bold;
      font-size: 1.05em; }
    body .container form .fa-times-circle {
      font-size: 1.15em;
      margin-left: 5px;
      color: #ff0000; }
    body .container form .fa-check-circle {
      font-size: 1.15em;
      margin-left: 5px;
      color: #00ff00; }
    body .container form .validator-icon {
      min-width: 20px; }
    body .container form .validate-error {
      display: none;
      color: #ff0000;
      padding-top: 5px;
      padding-bottom: 5px; }
  body footer {
    background: #f0e68c; }
  body .paymentBlock {
    margin: 20px; }
    body .paymentBlock div.paymentTitle {
      background: #9a031e;
      padding: 5px;
      text-align: center; }
    body .paymentBlock div.paymentBody, body .paymentBlock div.paymentOptions {
      padding: 10px; }
      body .paymentBlock div.paymentBody .user-loan-info li, body .paymentBlock div.paymentOptions .user-loan-info li {
        list-style-type: none; }
      body .paymentBlock div.paymentBody .user-loan-info span, body .paymentBlock div.paymentOptions .user-loan-info span {
        color: #9a031e;
        font-weight: bold; }
    body .paymentBlock .arrows-for-payment {
      display: inline; }
    body .paymentBlock .divTable {
      display: table;
      width: auto; }
      body .paymentBlock .divTable .divRow {
        display: table-row;
        width: auto; }
        body .paymentBlock .divTable .divRow .divCell {
          float: left;
          display: table-column; }
  body .clear {
    clear: both; }



